<template>
  <div class="container">
    <div class="select flex-sa">
      <div @click="selectType(1)" :class="{ current: isActive }">下次拜访</div>      
      <div  @click="selectType(0)" :class="{ current: !isActive }">今日拜访</div>      
    </div>
    <p v-if="isActive" @click="showTime" class="select-time">{{text}}</p>
    <p>
      <textarea v-model="content" placeholder="内容" class="content" name id cols="30" rows="10"></textarea>
    </p>

    <div class="button" @click="submtiLog">提交</div>
    <van-popup v-model="show" position="bottom" :style="{ height: '50%' }">
      <van-datetime-picker
        v-model="time"
        type="date"
        title="选择年月日"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="sureClick"
        @cancel="canceleClick"
      />
    </van-popup>
  </div>
</template>

<script>
import { addCustLog } from "@api/public";
import { DatetimePicker, Popup } from "vant";
import { trim } from "@utils";
export default {
  name: "MyMessage",
  components: {
    [Popup.name]: Popup,
    [DatetimePicker.name]: DatetimePicker,
  },
  data: function () {
    return {
      show: false,
      data: null,
      text: "点击选择下次拜访时间",
      time: new Date(),
      content: "",
      isActive:true
    };
  },
  created() {},
  mounted: function () {},
  methods: {
    showTime() {
      this.show = true;
    },
    canceleClick() {
      this.show = false;
    },
    selectType(e){
      this.isActive = e == 1 ? true : false;
    },
    sureClick(date) {
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      this.text = y + "-" + MM + "-" + d;
      this.show = false;
    },

    submtiLog() {
      if (this.isActive && this.text == "点击选择下次拜访时间") {
        this.$dialog.message("请选择拜访时间");
      } else if (this.content == "") {
        this.$dialog.message("请输入内容");
      } else {
        var data = {
          cust_no: this.$route.params.type,
          intention: 5,
          contents: this.content,
          start_time : this.isActive ? this.text : 1 
        };

        addCustLog(data).then((res) => {
          this.$dialog.message("添加成功");
          this.text = "点击选择时间";
          this.content = "";
        });
      }
    }
  },
};
</script>
<style scoped>
.container {
  background: #fff;
  padding: 10px;
}

.customer-analysis {
  padding: 20px;
}

.select-time {
  border: 1px solid #999;
  height: 35px;
  line-height: 35px;
  padding-left: 5px;
  width: 100%;
  font-size: 12px;
  padding-left: 10px;
  color: #999;
}
.select div{
  padding-bottom: 10px;
  border-bottom: 2px solid #fff;
  margin-bottom: 15px;
}
.select .current{
  border-bottom: 2px solid #092e48;
}
p {
  margin-bottom: 5px;
}

textarea {
  width: 100%;
  resize: none;
  padding: 10px;
  height: 200px;
}

.button {
  height: 35px;
  text-align: center;
  line-height: 35px;
  width: 100%;
  margin: 20px auto;
  color: #fff;
  background: #092e48;
}
.start_time {
  width: 100%;
}
</style>
